/* Import variables from the base folder*/
@import '../../assets/sass/base/variables';
@import '../../assets/sass/base/mixins';

.block__countdown_timer {
	@include sm-down {
		display: none;
	}

	/* &.is-pinned--bottom {*/
	/* 	z-index: 1;*/
	/* }*/

	/* Set default colours.*/
	&:not(.has-background) {
		background: rgba(35, 31, 32,0.8);
	}
	&:not(.has-text-color) {
		color: white;
	}

	.block__inner {
		@include block-content-max-width('medium');
	}

	.countdown_timer__counter {
		/* padding-top: weba-spacing(2);*/
		@include lg-up {
			margin-top: 50px;
			min-width: rem(375px);
		}
	}
	
	.countdown_timer__times {
		opacity: 0;
		display: flex;
		justify-content: center;
		min-height: 50px;
		@include lg-down {
			text-align: center;
		}
		h4 {
			margin-bottom: 0;
		}
		.timer__item {
			display: flex;
			flex-direction: column;
			align-items: baseline;
			justify-content: center;
			margin: 0 20px;
			@include sm-up {
				flex-direction: row;
				.timer__title {
					margin-left: rem(8px);
					font-size: rem(16px);
					text-transform: uppercase;
					letter-spacing: 2.4px;
					padding-top: 2px;
					font-weight: 600;
				}
				&.timer__item--minute {
					margin-left: rem(16px);
				}
			}
			.timer__value {
				margin-bottom: 0;
				font-size: rem(34px);
				line-height: rem(30px);
				font-weight: 700;
				font-family: var(--wp--preset--font-family--libre-baskerville);
				@include md-up {
					font-size: rem(54px);
					line-height: rem(54px);
				}
				@include lg-up() {
					font-size: rem(100px);
					line-height: rem(100px);
				}
			}
		}
		@include lg-up() {
			padding-top: 0;
			min-height: 30px;
			display: flex;
			align-items: center;
			min-width: 379px;
		}
	}
	
	.countdown_timer__wrapper {
		position: relative;
		z-index: 1;
		&::after {
			content: '';
			background: rgba(35, 31, 32, 0.9);
			mix-blend-mode: hard-light;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			position: absolute;
		}
		img {
			object-fit: cover;
			display: block;
			width: 100%;
			max-height: 527px;
		}
		@include lg-up() {
			display: flex;
			justify-content: space-evenly;
			align-items: center;
			.wp-block-heading {
				min-width: rem(430px);
			}
		}
	}

	/* Position the content over the image.*/
	.countdown_timer__content {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		height: 100%;
		.readible_date {
			font-size: rem(16px);
			letter-spacing: 2.4px;
		}
	
		.wp-block-heading {
			margin-bottom: 20px;
			text-align: center;
		}

		.countdown_timer__times {
			padding-left: weba-spacing(0.5);
			padding-right: weba-spacing(0.5);
			display: flex;
			flex-direction: column;
			@include sm-up() {
				flex-direction: row;
				justify-content: center;
				align-items: center;
			}
		}
	}
}

