// Variables
// -------------------
// Defines the main variables for use throughout all the other partials.
// It's mainly colours and a few default sizes / margins that are used through the theme.
// Breakpoint definitions are found in variables partial in the bootstrap folder.
// Main brand colours
$black: #000000;
$itij-red: #E63629;
$itij-red-aa: #db3124; // AA Accessible colour version (for white text).
$itij-red-darker: #911c1d;
$itij-light-grey: #F2F2F2;
$navigation-divider: #dadada;

$link-color: $itij-red;
$input-placeholder: #C0C0C0;
$input-border: #9d9d9d;
$input-border--hover-focus: #5A5A5A;
$input-radius: 13px;
$checkbox-border: $input-border;

// $input-color: #5A5A5A;
// Making darker as it doesn't have good contrast
$input-color: #2c2c2c;

// Font families
$base-font: "futura-pt", "sans-serif";
$heading-font: "Libre Baskerville", "Times New Roman", "serif";

// Font sizes
$base-font-size: 16px; // sets the spacer variable = 1rem 

// Font weights
$base-weight: 400;
$demi-weight: 500;

// ---------------------------
// Theme & Layout Variables
// ---------------------------
$width--alignwide: 1460px;
$width--container: 1400px; // Max wrapping width of all elements (e.g. header and footer widths).
$width--container--large: 1160px; // default width off all non .align content blocks.
$width--container--medium: 862px; // default width off all non .align content blocks.
$width--container--small: 768px;

// -------------------
// Breakpoint Variables
// -------------------
$breakpoint--xxs: 360px;
$breakpoint--xs: 400px;
$breakpoint--sm: 567px;
$breakpoint--md: 940px;
$breakpoint--lg: 992px;
$breakpoint--xl: calc($width--container + 16px);
$breakpoint--xl-mid: 1345px;  // largest width + gutters
$breakpoint--xxl: 1490px;

// Get values just below the max value for media breakpoints down:
$breakpoint--xxs--down: $breakpoint--xxs - 1;
$breakpoint--xs--down: $breakpoint--xs - 1;
$breakpoint--sm--down: $breakpoint--sm - 1;
$breakpoint--md--down: $breakpoint--md - 1;
$breakpoint--lg--down: $breakpoint--lg - 1;
$breakpoint--xl--down: $breakpoint--xl - 1;
$breakpoint--xxl--down: $breakpoint--xxl - 1;
